.navBar{
    width: 100%;
    position: fixed;
    top: 0;
    /* background-color: #111; */
    background-color: transparent;
    display: flex;
    justify-content: space-between;
    height: 70px;
    align-items: center;
    padding-top:10px;
    padding-bottom:10px;
    padding-left: 60px;
    padding-right: 60px;
    z-index: 10;
    font-size: smaller;
}
.navs{
    margin-left: 25px;
}
.imgNnav{
    display: flex;
}
.logo{
    width: 95px;
    height: 25px;
}
.avatar{
    width: 35px;
    border-radius: 5px;
}
a{
    margin-left:20px;
    color: white;
    font-weight: 10px;
}
.toggle{
    display: none;
}

@media(max-width:800px){
    .navs{
        display: none;
    }
    .rightNav{
        display: none;
    }
}