.banner{
    width: 100%;
    height: 85vh;
    
    background-size: cover;
    text-shadow: 1.5px 1.5px black;
}
.fade{
    width: 100%;
    height: 50px;
    background-image:linear-gradient(180deg,transparent,rgba(37,37,37,0.61),#111);
    position: absolute;
    bottom: 15vh;
}
.content{
    max-width: 500px;
    color: white;
    position: absolute;
    top: 30%;
    left: 5%;
    
}
button{
    width: 100px;
    height: 40px;
    background-color: #808080;
    color: white;
    outline: none;
    border: none;
    border-radius: 4px;
    font-size: medium;
    font-weight: bolder;
    margin-right: 20px;
}
.buttons{
    margin-top: 20px;
    margin-bottom: 20px;
}