.posterRow{
    width: 100%;
    margin-top: 40px;
}
h2{
    color: white;
    margin-left: 20px;
    margin-top: 20px;
}
.poster{
    width: 250px;
    height: 300px;
    margin-right: 10px;
    border-radius: 4px;
    cursor: pointer;
    transition: 0.5s;
}
.poster:hover{
    width: 300px;
    height: 350px;
}
.posters{
    height: 350px;
    display: flex;
    overflow-x: scroll;
    overflow-y: hidden;
    margin-top: 20px;
    align-items: center;
}
.posters::-webkit-scrollbar{
    display: none;
}
